@import 'colors';
@import 'typography';

*{
    box-sizing:border-box;
}
body,html{
    width: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden; 
}



#particles-js {
   position:absolute;
   min-width: 100%;
   min-height: 100%;
    background-color: #000c1e;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    z-index: -1;
    height: 100%;
}
.hero-overlay{
width:100%;
height:100vh;
}
.hero-text{
    padding-top: 30vh;

h1{
    font-weight: 900;
    font-size: 4em;
    color: rgba(255, 255, 255, 0.8);
}
h2{
    font-size: 1.3em;
    font-weight: bold;
    color: rgba(255, 255, 255, 0.6);
    padding-top: 2em;
}
}

  header,
  section{
      overflow-x:hidden;
  }

header{
}

.hero{
}


.intro{
background-color: red;
background: yellow; //-url(/assets/images/image-one.jpg);
background-size: 100% auto;
background-repeat: no-repeat;
overflow: hidden;
width:100%;
padding: 80px 0 300px 0;
}


.benefits-panel {
    background: $nrbs-blue; //-linear-gradient(45deg,rgba(19,29,38,1) 0%,rgba(31,48,64,1) 100%);
    padding: 280px 0;
}

.benefits-table {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
}

.benefits-item {
    width: 25%;
    height: 100%;
    padding: 0px 22px 0 22px;
    text-align: center;
    color: #FFFFFF;
    font-size: 16px;
    line-height: 24px;
}

.benefits-top,.benefits-top-2 {
    padding-top: 0;
}

.benefits-content {
    margin: 40px 0 0 0;
}

.benefits-title {
    color: #23bec6;
    font-weight: 900;
    font-size: 20px;
    margin: 0 0 15px 0;
}

.benefits-icon {
    height: 60%;
    max-height: 95px;
}

@media (min-width: 768px) and (max-width:991px) {
    .benefits-panel {
        padding:120px 0;
    }

    .benefits-title {
        font-size: 20px;
        margin: 0 0 20px 0;
    }

    .benefits-item {
        width: 50%;
        font-size: 16px;
        line-height: 22px;
        margin: 40px 0;
    }

    .benefits-icon {
        height: 50%;
        max-height: 70px;
    }

    .benefits-content {
        margin: 20px 0 0 0;
    }
}

@media (max-width: 767px) {
    .benefits-panel {
        padding:80px 0;
    }

    .benefits-item {
        display: block;
        width: 100%;
        padding: 0 20px 80px 20px;
        text-align: center;
        color: #FFFFFF;
        font-size: 15px;
        line-height: 20px;
    }

    .last-benefit {
        padding: 0 20px 0px 20px;
    }

    .benefits-title {
        font-size: 18px;
        margin: 0 0 15px 0;
    }

    .benefits-icon {
        height: 60%;
        max-height: 85px;
    }
}

.features-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
}

.features-img{
    width: 50%;
    height: 65vh;
    background: red;
}
.features-info{
    width: 50%;
}
.features-info-text{
    width: 50%;
    margin: 0 auto;
}

@media (min-width: 768px) and (max-width:991px) {
    .features-img {
        height:45vh;
    }

    .features-info-text {
        font-size: 16px;
    }
}

@media (max-width: 767px) {
    .features-info-text {
        font-size:15px;
        width: 90%;
        margin: 0 auto;
        padding: 80px 20px;
    }

    .features-info {
        width: 100%;
        order: 1;
    }

    .features-img {
        width: 100%;
        height: 25vh;
        order: 2;
    }
}



.contact-panel{
    background: blanchedalmond;
    padding: 180px 0;
    text-align: center;
}

.footer-panel{
    background: red;
    padding: 180px 0;
    text-align: center;
}



.navbar-nav .nav-link > a{
    color: red;
}


.scrolled{
    background: white;
    .nav-link{
        color: $nrbs-blue !important;
        background: red !important;
        
        &:hover {
            color: #00f0ff !important;
            background: $nrbs-blue !important;
        }
    }
    .navbar-brand{
        color: $nrbs-blue !important;
        &:hover {
            color: #00f0ff !important;
        }
    }


}

.not-scrolled{
    background: transparent;
    .nav-link{
        
        &:hover {
            color: #00f0ff !important;
            background: $nrbs-blue !important;
        }
    }
}

@media (min-width: 991px){
    .fixed-top{
        padding: 0 25vw !important;
        }
    
    .nav-link{
        padding: 25px !important; 
    }
}
@media (max-width: 992px){
    
    .navbar-toggler{
    border: 0 !important;
    margin-right: 30px !important;
    }
    .navbar{
    padding: 0 0 !important;
    }

    .navbar-brand{
    padding: 20px 30px !important;
    }
    .nav-link{
    padding-left: 30px !important;
    }
}

.contact-btn{
    background: #23bec6;
    padding: 8px 20px;
    border-radius: 5px;
    font-size: 18px;
    color: #FFFFFF;
    text-align: center;
    text-decoration: none;
}